* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    font-family: 'Josefin Sans', sans-serif;
}

header {
    width: 100%;
    height: 100vh;
    background-image: url('../src/image/bg.png');
    background-repeat: no-repeat;
    background-size: 100% 100%;
}

.mainheader {
    width: 100%;
    height: 100px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.mainheader .logo img {
    width: 200px;
    height: 70px;
    padding-left: 60px;
}

.mainheader .navi {
    width: 450px;
    display: flex;
    justify-content: space-around;
    align-items: center;
}

.help {
    margin-right: 60px;
}


.mainheader button {
    padding: 10px 45px;
    text-align: center;
    font-size: 14px;
    color: #fff;
    border: none;
    background-image: linear-gradient(to right, #649bff, #0070fa, #649bff);
    border-radius: 10px;
}

main {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

main .right-sec {
    padding-right: 70px;
}

main .left-sec {
    padding-left: 100px;
    padding-top: 100px;
}

.left-sec h2 {
    font-size: 20px;
    text-transform: capitalize;
    font-weight: lighter;
    color: #242424;
    margin-top: 100px;
}

.left-sec h1 {
    font-size: 55px;
    text-transform: capitalize;
    font-weight: 700;
    margin: 15px 0;
}

.left-sec p {
    margin-bottom: 20px;
}
.service .head{
    margin-top: 25px;
    display: flex;
    justify-content: center;
    align-items: c;
    
}

.levels{
    display: flex;
    justify-content: center;
    align-items: center;
}
.heart{
    padding: 50px;
}
.oxygen{
    padding: 50px;
}
.service .help .back{
    display: inline-block;
    padding: 10px 45px;
    /* text-align: center; */
    font-size: 14px;
    color: #fff;
    border: none;
    background-image: linear-gradient(to right, #649bff, #0070fa, #649bff);
    border-radius: 10px;
    margin-left: 700px;
   
}
